  .wrapper {
    max-width: 1100px;
    width: 100%;
    position: absolute;
    top: 1507px;
    left: 250px;
  
  }
  .wrapper button {
    top: 50%;
    height: 50px;
    width: 50px;
    cursor: pointer;
    font-size: 1.25rem;
    position: absolute;
    text-align: center;
    line-height: 50px;
    background: #fff;
    border-radius: 50%;
    border: none;
    box-shadow: 0 3px 6px rgba(0,0,0,0.23);
    transform: translateY(-50%);
    transition: transform 0.1s linear;
  }
  
  .wrapper button:active{
    transform: translateY(-50%) scale(0.85);
  }
  .wrapper button:first-child{
    left: -150px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.23);
  
  }
  .wrapper button:last-child{
    right: -150px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.23);
  }
  .wrapper .carousel{
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: calc((100% / 3) - 12px);
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    gap: 16px;
    border-radius: 8px;
    scroll-behavior: smooth;
    scrollbar-width: none;
  }
  .carousel::-webkit-scrollbar {
    display: none;
  }
  .carousel.no-transition {
    scroll-behavior: auto;
  }
  .carousel.dragging {
    scroll-snap-type: none;
    scroll-behavior: auto;
  }
  .carousel.dragging .card {
    cursor: grab;
    user-select: none;
  }
  .carousel :where(.card, .img) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .carousel .card {
    scroll-snap-align: start;
    height: 500px;
    list-style: none;
    background-color:#61bfab;
    
    /* background: rgba(71, 181, 158, 0.2); */
    cursor: pointer;
    padding-bottom: 15px;
    flex-direction: column;
    border-radius: 8px;
    
  }
  /* .carousel .card .img {
    background: #8B53FF;
    height: 58px;
    width: 58px;
    border-radius: 50%;
  } */
  .card .img img {
    width: 58px;
    height: 58px;
    /* padding-top: 20px; */
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid #fff;
  }
  .carousel .card h2 {
    font-weight: 500;
    font-size: 1.56rem;
    /* margin: 30px 0 5px; */
    padding: 20px;
  }
  .carousel .card span {
    color: #6A6D78;
    font-size: 1.31rem;
    padding: 20px;
  }